/* Header.css */
.header {
  color: #fff;
    padding: 0 10px 0 10px;
    align-items: center;
    display: flex;
    position: absolute;
    z-index: 2;
    width: -webkit-fill-available;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  }
  
  .logo {
    width: 100px;
  }

  .burger-menu {
    margin-left: auto;
    width: 34px;
    cursor: pointer;
  }
  
  .modal {
    /* Стили для модального окна до анимации */
    position: fixed;
    top: 0;
    right: -300px; /* Начальное положение справа за пределами экрана */
    width: 300px;
    height: 100%;
    background-color: rgb(0, 0, 0);
    transition: right 0.3s ease; /* Добавляем анимацию с плавным эффектом */
    z-index: 1000;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .modal.open {
    right: 0;
  }

  .cross-btn {
    width: 30px;
    position: absolute;
    right: 0;
    margin: 10px
  }

  .burger-menu-list {
   padding-top: 20%;
   text-align: center;
  }

  .burger-list-item {
    padding-top: 20px;
  }

  .burger-menu-text {
   font-size: 35px;
  }

  .logo-menu {
    padding-top: 30px;
    width: 150px;
  }